.nav-padding {
  height: 100px; }


#nav {
  @include position(fixed, 0 null null null );
  @include size(100%, 100px);
  box-shadow: 0 0 10px rgba(200,200,200,0.95);
  background-color: rgb(254, 254, 254);
  z-index: 20;

  .container {
    @include f(null, center);
    @include size(100%);
    position: relative; }

  .logo {
    margin-right: 55px;
    min-width: 75px; }

  .nav_office,
  .nav_shop {
    @include f(null, center);

    li:first-child a {
      padding-left: 0; }

    li:last-child a:after {
      border-right: 0px solid #000; } }


  .nav_office {
    padding-bottom: 15px;
    li a {
      padding: 0 13px;
      font-size: 15px;
      color: #959595;
      @include afterline(16px);
      @include hoverText(black); } }



  .nav_shop {
    padding-bottom: 5px;
    li a {
      padding: 0 20px;
      font-size: 17px;
      font-weight: bolder;
      line-height: 22px;
      @include hoverText($purple);
      @include afterline(22px);

      &.current {
        color: $purple; } } }



  .float {
    @include position(absolute, 0 0 null null );
    @include f;
    @include padding(18px 25px); }

  .login {
    padding-right: 12px;
    @include afterline(22px);
    font-weight: bold;
    &:before {
      margin-right: 10px;
      font-size: 1.2rem; }

    @include hoverText($purple); }



  .cart {
    @include fc;
    padding-left: 12px;
    line-height: 22px;
    &:before {
      font-size: 1.2rem; }
    .bedge {
      @include size(19px);
      background-color: rgb(179, 119, 49);
      border-radius: 50%;
      color: white;
      @include fc;
      font-size: 10px;
      @include position(relative, -8px 0 null null );
      opacity: 0; }


    @include hoverText($purple); } }



.nav-padding {
  @include max(1365) {
    height: 60px; } }

#nav {
  @include max(1365) {
    height: 60px;

    .logo img {
      height: 40px; }

    .nav_office {
      display: none; }

    .nav_shop {
      padding-bottom: 0; } }


  @include max(767) {
    .logo {
      margin-right: 0; }

    .float {
      position: relative;
      padding-top: 22px;
      padding-right: 5px; }

    .nav__content {
      @include f(flex-end);
      margin-left: auto; }


    .nav__toggle {
      cursor: pointer;
      z-index: 14;
      @include padding(20px);
      position: relative;
      transition: 0.3s transform;

      .one, .two, .three {
        @include size(20px, 2px);
        background: $purple;
        margin: 4px auto;
        backface-visibility: hidden;
        transition: 0.3s transform; } }

    #nav_cover {
      @include position(fixed, 60px 0 null null );
      background-color: #fff;
      width: 100%;
      box-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.1), 0 3px 5px rgba(0, 0, 0, 0.1); }
    .nav_shop {
      @include padding(20px 30px);
      align-items: flex-end;
      flex-direction: column;
      li a {
        @include padding(10px 15px);
        &:after {
          border-right: none; } } }

    #nav_cover {
      height: 0;
      overflow: hidden;
 }      // display: none

