    background-size: cover;
    height: 350px;
    margin-bottom: 35px;
    @include fc;
    @include padding(15px);

    .slogan {
      color: white;
      text-align: center;
      text-shadow: 0 0 100px rgba(black, 0.3), 0 0 10px rgba(black, 0.3); }
    .f56 {
      margin-bottom: 0; }
    .f24.bd {
      margin-bottom: 6px;
