.inquiryWrap {
  border-top: 1px solid $brown;
  width: 100%; }

.inquiryLog {
  .log__title {
    height: 50px;
    background-color: rgb(235, 235, 235);
    @include f(flex-start,center);
    @include padding(null 30px);
    border-bottom: 1px solid rgb(235, 235, 235); }

  .log__content {
    @include padding(20px 30px);
    @include f(flex-start,center);
    border-bottom: 1px solid rgb(235, 235, 235);
    > .lh20 {
      width: 100%; } }

  .sub {
    @include f(flex-start,flex-end);
    margin-right: 50px;
    flex-shrink: 0;
    span {
      margin-left: 10px; } }

  ul {
    @include f;
    color: #7C7C7C; }
  li {
    margin-right: 35px;
    white-space: nowrap; }

  .answer {
    background-color: rgb(254, 246, 235);
    .sub {
      color: rgb(203, 152, 95); } }

  @include max(990) {
    ul {
      flex-wrap: wrap;
      margin-bottom: 10px; }
    li {
      width: 100%;
      margin-right: 0; } }

  @include max(580) {
    .log__content {
      flex-direction: column;
      align-items: flex-start;
      @include padding(null 20px); }

    .sub {
      @include margin(null 0 20px null  ); }
    ul {
      font-size: 15px; } } }




#modal-cancel {
  max-width: 600px;
  .f24 {
    color: $brown-dark; }

  .modal__main {
    background-color: rgb(245, 241, 240);
    @include padding(48px 60px 40px);

    @include max(400) {
      @include padding(30px 40px); } } }

