

$defaultcolor: rgb(22, 24, 23) !default;
$red: rgb(166, 42, 6);
$red-btn: rgb(226, 64, 0);
$purple: rgb(139, 29, 90);
$purple-light: rgb(154, 38, 102);
$orange: rgb(247,181,80);
$brown-dark-f: rgb(165, 123, 75);

$brown-dark: rgb(180, 120, 50);

$brown: rgb(188, 159, 119);
$brown-230: rgb(230, 210, 182);

$brown-240: rgb(240, 234, 221);
$brown-light: rgb(246, 242, 241);

$table-bgc: rgb(249, 245, 234);

$white: rgb(254, 254, 254);
$blue: rgb(145,199,213);


.bold {
  font-weight: bold; }

.underline {
  text-decoration: underline; }

.brown {
  color: #B47832; }

.red {
  color: #CC0000; }

.gray {
  color: #7C7C7C; }

.vege {
  color: #59a467; }

.italicNum {
  font-family: 'Open Sans', sans-serif;
  color: #cc0000; }



.line {
  width: 100%;
  @include f(flex-start,center);
  position: relative;
  padding-left: 96px;

  > span {
    @include position(absolute, 0 null null 0);
    line-height: 40px;
    width: 66px; }

  > span:nth-child(2) {
    @include size(100%, 40px);
    position: static; }

  input {
    @include size(100%, 40px);
    line-height: 40px;
    @include padding(null 15px);
    border: 1px solid $brown;
    border-radius: 3px;
    color: $brown-dark;
    @include trans;
    &::placeholder {
      color: $brown-dark; }
    &:hover {
      background-color: $brown-light; }
    &:focus {
      color: $brown-dark;
      border-color: $brown-dark; } } }


.radio_unit {
  height: 45px;
  white-space: nowrap;

  input {
    position: absolute;
    opacity: 0;
    pointer-events: none; }

  label {
    cursor: pointer;
    @include size(100%);
    @include fc;
    &:before {
      content: "";
      display: block;
      @include size(18px);
      background: $white;
      border-radius: 50%;
      border: 1px solid rgb(211, 174, 130);
      transition: background-color 0.3s ease, box-shadow 0.3s ease;
      flex-shrink: 0; } }

  input:checked + label:before {
    background-color: rgb(211, 174, 130);
    box-shadow: inset 0 0 0 3px #f4f4f4; }

  input:disabled + label:before {
    box-shadow: inset 0 0 0 4px #f4f4f4;
    border-color: #b4b4b4;
    background: #b4b4b4; } }


.select_unit {
  @include f(center, center);
  width: 240px;
  position: relative;
  white-space: nowrap;

  &:after {
    content: "";
    display: block;
    @include position(absolute, 16px 16px null null );  //20-4
    @include triangle("down", 12px, 8px, $brown);
    pointer-events: none; }

  select {
    @include size(100%, 40px);
    line-height: 40px;
    @include padding(null 15px);
    border: 1px solid $brown;
    border-radius: 3px;
    color: $brown-dark;
    @include trans;

    &::-ms-expand {
      display: none; }
    &:hover {
      background-color: $brown-light; }
    &:focus {
      border-color: $brown-dark; }
    &::placeholder {
      color: $brown-dark; } } }


.search_unit {
  @include f;
  @include size(260px, 40px);
  position: relative;

  input[type="search"] {
    @include size(100%);
    border: 1px solid $brown;
    @include padding(null 15px);

    @include trans;
    &::placeholder {
      color: $brown-dark; }
    &:focus {
      border-color: $brown-dark; }
    &:hover {
      background-color: $brown-light; } }

  input[type="submit"] {
    @include size(38px,100%);
    opacity: 0;
    @include position(absolute, null 0 null null ); }
  &:before {
    @include position(absolute, 12px 8px null null );
    color: $brown;
    font-size: 1rem; } }


.check_unit {
  cursor: pointer;
  @include f(null, center, row);
  position: relative;

  input {
    display: none; }

  .box {
    display: inline-block;
    @include size(18px);
    background: $white;
    border: 1px solid $brown;
    margin-right: 15px;
    border-radius: 3px;
    flex-shrink: 0;
    box-shadow: none;

    &:after {
      content: "";
      position: absolute;
      @include size(0, 3px);
      background: $brown-dark;
      transform: translate(6px, 11px) rotate(-50deg);
      transform-origin: left;
      transition: all 50ms;
      transition-delay: 0ms;
      border-radius: 20% 60% 60% 20%; }

    &:before {
      content: "";
      position: absolute;
      @include size(0, 3px);
      background: $brown-dark;

      transform: translate(4px, 4px) rotate(75deg);
      transform-origin: left;
      transition: all 50ms;
      transition-delay: 50ms;
      border-radius: 60%; } }


  input:checked {
    & + .box {
      &:before {
        width: 8px;
        transition-delay: 0ms; }
      &:after {
        width: 18px;
        transition-delay: 50ms; } } } }



