


.footer {
  background-image: url("./images/00-foot-bgc2.jpg");
  background-size: cover;
  white-space: nowrap;
  color: rgb(240,223,196);
  position: relative;
  overflow: hidden;
  a {
    color: rgb(240,223,196);
    @include hoverText(#fff); } }
.flower1 {
  @include position(absolute, 0 0 null null );
  transform: translate(40%, -30%); }
.flower2 {
  @include position(absolute, null null 0 0);
  transform: translate(-30%, 45%); }

.sitemap {
  @include container;
  @include f(flex-start);
  @include padding( 70px null 60px null);
  border-bottom: 1px solid rgba(256,256,256,0.3);
  padding-bottom: 40px;

  &__content {
    width: 100%;
    margin-right: 120px;
    @include f(space-between, flex-start);

    li {
      @include f(flex-start, flex-start, column);

      a {
        margin-bottom: 14px; }
      a.f20 {
        text-shadow: 0 0 1px white;
        margin-bottom: 20px;
        letter-spacing: 1px; } }

    li.secondary {
      font-weight: bold; } }

  &__call {
    margin-top: -15px;
    li {
      @include size(20.83vw, 56px);
      min-width: 300px;
      @include f(flex-start, center);
      letter-spacing: 1px;
      a {
        margin: 0 5px; }

      &:not(:last-child) {
        border-bottom: 1px solid rgba(256,256,256,0.3); } } }

  @include m(1200, 991) {
    @include padding(30px null 15px);

    &__content {
      margin-right: 60px; }

    &__call {
      margin-top: 0;
      li {
        @include size(auto);
        min-width: 0px;
        border-bottom: 0 !important;
        font-size: 16px;
        @include f(center,flex-start,column);
        a {
          @include margin(5px null 15px); } } } }

  @include max(990) {
    @include padding(30px 60px 15px);

    &__content {
      display: none; }

    &__call {
      margin-top: 0;
      width: 100%;
      @include f(center,center,row, wrap);

      li {
        @include margin(null null 15px);
        @include size(50%, auto);
        min-width: 0px;
        border-bottom: 0 !important;
        font-weight: bolder;
        font-size: 18px; } } }

  @include max(768) {
    &__call li {
      @include size(100%, auto); } }

  @include max(550) {
    padding: 50px 30px 35px; }

  @include max(370) {
    padding: 40px 20px 10px;
    li {
      font-size: 16px; } } }


.address {
  @include padding( 40px null);
  @include f(center, flex-end);
  font-style: normal;
  @include container;

  &__logo {
    @include size(180px, auto);
    margin-right: 30px;
    min-height: 1px; }

  &__content {
    @include f(flex-start,flex-start,column);
    width: 100%;
    max-width: 760px;

    > * {
      @include f(flex-start,flex-start);
      width: 100%; } }

  &__location {
    @include margin(null null 10px -8px);
    li {
      padding: 0 8px; }
    li:not(:first-child):not(:nth-last-child(2)):not(:last-child) {
      border-right: 1px solid white; }
    li:last-child a {
      text-decoration: underline; } }

  &__contact {
    @include margin(null null 16px -10px);
    a {
      margin: 0 10px; } }

  @include max(990) {

    @include f(center, flex-start, column);
    padding: 30px 60px;

    &__logo {
      margin-bottom: 30px;
      width: 18vw;
      min-width: 120px; }

    &__location {
      @include f(flex-start, flex-start, row ,wrap);
      margin-bottom: 0px;
      &::after {
        content: "";
        width: calc(100% - 450px); }

      li.linebreak ~ li {
        order: 1; }
      li {
        margin-bottom: 10px; } } }

  @include max(768) {

    .tel ~ * {
      display: none; }

    &__copyright {
      flex-wrap: wrap;

      span:nth-child(1) {
        width: 100%;
        margin-bottom: 5px; }

      span:nth-child(2) {
        display: none; } } }

  @include max(550) {
    padding: 30px 30px;
    font-size: 14px;
    .f14 {
      font-size: 14px; }

    &__location {
      @include margin(null null 8px -6px);
      &::after {
        display: none; }
      li {
        padding: 0 6px; } }

    &__contact {
      flex-wrap: wrap;
      .addr {
        width: 100%;
        margin-bottom: 5px;
        & + span {
          display: none; } } } }

  @include max(370) {
    padding: 30px 20px;
    .f14 {
      font-size: 12px; } } }
