
#register {
  background-image: url("./images/login-00-bgc4.jpg");
  // background-position:  center top -60px
  background-position:  right top -60px;
  // background-size: cover

  .title {
    @include margin(null auto);
    max-width: 1200px;
    width: 90%;
    border-bottom: 2px solid rgba(#B47832, 0.5);
    padding-bottom: 12px;
    padding-top: 25px;
    .f24 {
      color: #A57B4B; } }

  .submit {
    @include margin(null auto);
    max-width: 1200px;
    width: 90%;
    border-top: 2px solid rgba(#B47832, 0.5); }


  .group.md input {
    max-width: 350px; }

  .box:first-child {
    margin-bottom: 5px;
    .box__content {
      padding-bottom: 30px; } }

  .verify {
    img {
      cursor: pointer; }
    input {
      max-width: 180px; } }

  @include max(580) {
    .group.addr {
      > .spe1 {
        @include margin(null 0 5px null); } }


    .box__title {
      .ml24 {
        display: block;
        margin-left: 0; } }

    .verify {
      span.ml16 {
        display: none; } } }

  @include max(390) {
    .verify {
      flex-wrap: wrap;
      input {
        max-width: 100%; }
      img {
        @include margin(8px null  null 0); } } } }
