


.pages {
  @include f(center, center);
  a {
    display: block;
    margin: 0 4px;
    @include f(center, center);
    @include size(27px);
    color: $brown-dark;
    border-radius: 50%;
    font-size: 14px;
    flex-shrink: 0;
    &:not([class^="icon"]) {
      border: 1px solid $brown-dark; }

    &:hover,
    &.active {
      background-color: $brown-dark;
      color: white; } } }

@mixin afterline($height, $color: rgb(199,199,199)) {
  line-height: $height;
  position: relative;
  &:after {
    content: "";
    display: block;
    border-right: 1px solid $color;
    @include position(absolute, 0 0 null null );
    height: $height; } }


.breadcrumb {
  @include padding( 20px null);
  white-space: nowrap;
  ul {
    @include f(flex-start, center);
    li:before {
      color: $orange;
      margin: 0 10px;
      font-weight: bolder; } }

  @include max(330) {
    li:nth-child(3) {
      display: none; } } }


.slcik-menu {
  border-top: 1px solid $brown-230;
  box-shadow: inset 0 -4px rgb(240, 231, 222);
  @mixin shorterLine {
    content: "";
    @include size(0px ,30px);
    border-right: 1px solid $brown-230;
    @include position(absolute, 50% 0 null null);
    transform: translateY(-50%); }

  ul {
    max-width: 1400px;
    @include margin(null auto);
    @include f; }
  li {

    flex: 1 1 auto;
    @include min(990) {
      max-width: 175px; } }  //會跟slcik有衝突

  a {
    height: 58px;
    @include fc;
    position: relative;
    font-weight: bold;
    // z-index: 1
    // line-height: 1.5
    text-align: center;
    &:after {
      @include shorterLine; } }

  li:first-child {
    a:before {
      @include shorterLine;
      left: 0px; } }

  li.active {
    border-bottom: 4px solid $brown-dark;
    a {
      color: $brown-dark;
      font-weight: bold;
      letter-spacing: 1px; } }

  li:hover {
    a {
      color: $brown-dark;
      transition: color 0.3s; } }

  .slick-arrow {
    @include size(40px , 100%);
    @include fc;
    height: 58px;
    position: absolute; }

  .slick-arrow {
    color: #F7B550; }

  .icon-prev {
    left: 0;
    &:after {
      @include shorterLine; } }
  .icon-next {
    right: 0;
    &:after {
      @include shorterLine;
      left: 0; } }


  @include max(990) {
    ul {
      @include padding(null 40px); } } }


.epapper {
  .unsubscribe {
    display: none; }

  width: 100%;
  max-width: 530px;
  border: 1px solid #D1AF81;
  box-shadow: 3px 3px 15px rgba(black,0.2);
  background-color: #fff;
  text-align: center;
  border-radius: 10px;
  overflow: hidden;

  .epapper__toggle {
    height: 50px;
    @include fc;
    background: linear-gradient(to bottom, rgb(255,255,255), rgb(249,245,235));
    border-bottom: 1px solid rgb(239,239,239);

    > div {
      @include size(120px, 100%);
      @include fc;
      cursor: pointer; }

    > .current {
      font-weight: bold;
      color: rgb(142, 108, 80);
      box-shadow: inset 0 -2px 0px rgb(222, 207, 186); } }


  .epapper__main {
    @include padding(36px 80px 64px);
    text-align: center; }

  .f24 {
    margin-top: 5px; }

  input {
    @include size(100%, 40px);
    border: 1px solid $brown;
    @include padding(null 15px);
    color: $brown;
    @include trans;
    &::placeholder {
      color: #A57B4B; }
    &:hover {
      background-color: $brown-light; }
    &:focus {
      color: #A57B4B;
      border-color: #A57B4B; } }

  .verify {
    @include f(flex-start, center);
    img {
      cursor: pointer; } }

  .login_btn {
    @include fc;
    > a {
      color: $white;
      @include size(170px, 40px);
      @include fc;
      background-color: rgb(99, 99, 99);
      border-radius: 3px;
      @include hoverBgc(rgb(63, 63, 63)); }

    .mr {
      margin-right: 22px;
      background-color: #A57B4B;
      @include hoverBgc(#d37d1b); } }


  .epapper__main {
    @include max(767) {
      @include padding(25px 40px 40px); }

    @include max(374) {
      @include padding(25px 30px 30px);
      .login_btn .mr {
        margin-right: 8px; } } } }




.modal {
  width: 100%;
  max-width: 530px;
  border: 1px solid #D1AF81;
  box-shadow: 3px 3px 15px rgba(black,0.2);
  background-color: #fff;
  text-align: center;
  border-radius: 6px;
  overflow: hidden;

  .modal__main {
    @include padding(48px 60px 64px);
    text-align: center; }

  .f24 {
    margin-top: 5px; }

  .modal-bottom {
    height: 136px;
    width: 100%;
    @include f(center,center);
    border-top: 1px solid #D1AF81;

    > a {
      color: rgb(178, 138, 89);
      @include hoverText(#d37d1b);
      @include f(center,center, column);

      [class*="icon"] {
        font-size: 1.8rem;
        margin-bottom: 10px; }

      .icon-user {
        font-size: 1.1rem;
        border: 2px solid rgb(178, 138, 89);
        border-radius: 50%;
        @include size(40px);
        @include fc; } }

    > a:hover .icon-user {
      border-color: #d37d1b; }

    > a:not(:last-child) {
      margin-right: 70px; } }

  .modal__main {
    @include max(767) {
      @include padding(25px 40px 40px); }

    @include max(374) {
      @include padding(25px 30px 30px); } } }


.modal.lg {
  max-width: 600px;
  .modal__main {
    @include padding(48px 60px 40px);

    @include max(767) {
      @include padding(25px 40px 40px); }

    @include max(374) {
      @include padding(null 20px 30px); } } }


.modal.dark {
  z-index: 2;
  .modal__main {
    @include padding(48px 50px 40px);
    background-color: rgb(244, 240, 239); } }







.plain {
  width: 100%;

  table {
    text-align: center;
    white-space: nowrap;
    width: 100%; }

  tr {
    border-bottom: 1px solid #E5E5E5;
    &:first-child {
      background-color: rgb(202, 153, 97);
      color: white; }
    &:last-child {
      border-bottom: 1px solid rgb(202, 153, 97); }
    &:nth-child(odd):not(:first-child) {
      background-color: #F6F6F6; } }

  a {
    color: #B47832;
    text-decoration: underline; }

  @include max(991) {
    font-size: 15px; } }


.control-row {
  @include f;
  .f24 {
    margin-right: auto; }
  .group {
    @include f; }

  .select_unit select,
  .search_unit input[type="search"] {
    border: 1px solid #ddd;
    color: #777;
    &::placeholder {
      color: #777; } }


  @include max(990) {
    flex-wrap: wrap;
    justify-content: space-between;

    .search_unit {
      width: calc(40% - 15px);
      order: 2; }

    .select_unit.md {
      order: 3; }

    .select_unit {
      margin-bottom: 0.8rem;
      margin-right: 0;
      width: calc(60% - 15px);
      span {
        width: 100px;
        flex-shrink: 0; } } }


  @include max(550) {
    .select_unit {
      order: 1 !important; }
    .f24,
    [class*="_unit"] {
      width: calc(100%);
      @include margin(null 0 1rem null);
      span {
        width: 90px; } }
    .search_unit {
      width: calc(100% - 90px);
      margin-left: auto; } } }





