.submit {
  border-top: 1px solid $brown;
  @include fc;
  .btn {
    @include size(200px, 38px);
    margin: 0 15px;
    padding: 0;
    line-height: 38px;
    color: white;
    border-radius: 3px; }

  .btn:not(.cancel) {
    background-color: $brown;
    @include hoverBgc($brown-dark); }

  .cancel {
    background-color: rgb(100, 100, 100);
    @include hoverBgc(rgb(60, 60, 60)); }

  @include max(500) {
    @include padding(null 10px);
    .btn {
      margin: 0 10px; } } }



