

#member {
  background-size: cover;
  max-height: 800px;
  height: 90vh;
  min-height: 650px;
  position: relative;



  @include m(768,720) {
    background-position: left center;
    background-size: 200%; }

  &.darker {
    &::before {
      content: "";
      @include position(absolute, 0 0 0 0);
      @include size(100%);
      background-color: rgba(black,0.2);
      z-index: 1; } }

  .container {
    @include size(100%);
    position: relative; }


  .breadcrumb {
    color: white;
    @include position(absolute, 0 null null 12px); }

  .login {
    @include size(1000px , 540px);
    background-color: $white;
    border-radius: 5px;
    @include fc; }

  .login_left,
  .login_right {
    @include size(50%, 100%);
    @include fc; }

  .login_left {
    @include padding(20px 55px 20px 70px);
    background-color: $brown-light;
    .f24 {
      font-weight: bold; }
    .mb16 {
      color: #683700; }
    input {
      @include size(100%, 60px);
      border: 1px solid $brown;
      padding: 15px;
      color: $brown;
      @include trans;
      &::placeholder {
        color: #A57B4B; }
      &:hover {
        background-color: $brown-light; }
      &:focus {
        color: #A57B4B;
        border-color: #A57B4B; } }

    .verify {
      @include f(flex-start, center);
      img {
        cursor: pointer; } }

    .login_btn {
      color: $white;
      @include size(100%, 60px);
      background-color: #A57B4B;
      @include fc;
      @include hoverBgc(#d37d1b); }

    .btn-group {
      @include f(center, center);
      line-height: 1;

      a:first-child {
        padding-right: 15px;
        margin-right: 15px;
        border-right: 2px solid black;
        font-weight: bold; }
      a {
        @include hoverText(#A57B4B); } } }


  .circle {
    border-radius: 50%;
    position: absolute;
    @include size(60px);
    color: #D5C3C3;
    left: 0;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    @include fc; }


  .login_right {
    @include padding(20px 70px 20px 50px);
    position: relative;
    .login_center {
      @include f(center, center, column); }

    .brown.mb24 {
      align-self: flex-start; }

    a:hover img {
      transition: 0.2s box-shadow;
      box-shadow: 0 0 10px rgba(100, 100, 100, 0.5); }

    .check_unit {
      align-items: flex-start;
      .box {
        margin-top: 3px; }

      a {
        text-decoration: underline;
        font-weight: bolder;
        @include hoverText(#A57B4B); } } } }

#member {
  @include max(990) {
    .login_left,
    .login_right {
      @include padding(40px);

      input, .login_btn {
        height: 50px; } } }

  @include max(767) {
    .login {
      display: block; }
    .login_left,
    .login_right {
      @include size(100%, auto); }

    .circle {
      top: 0;
      left: 50%; } }

  @include max(500) {
    .login_left, {
      @include padding(30px 30px 45px ); }
    .login_right {
      @include padding(30px); }

    .login {
      height: 80vh;
      @include margin(null 10px); } } }


.forget {

  width: 100%;
  max-width: 580px;
  border-radius: 8px;
  border: 1px solid #D1AF81;
  @include padding(70px 105px);
  box-shadow: 3px 3px 15px rgba(black,0.2);
  background-color: rgb(246, 242, 241);
  text-align: center;


  .f24 {
    font-weight: bold; }
  .f24 + * {
    @include margin(null -25px);
    color: #683700; }


  input {
    @include size(100%, 60px);
    border: 1px solid $brown;
    padding: 15px;
    color: $brown;
    @include trans;
    &::placeholder {
      color: #A57B4B; }
    &:hover {
      background-color: $brown-light; }
    &:focus {
      color: #A57B4B;
      border-color: #A57B4B; } }


  .verify {
    @include f(flex-start, center);
    img {
      cursor: pointer; } }

  .login_btn {
    @include fc;
    > a {
      color: $white;
      @include size(50%, 60px);
      @include fc;
      background-color: rgb(99, 99, 99);
      border-radius: 3px;
      @include hoverBgc(rgb(63, 63, 63)); }

    .mr {
      margin-right: 22px;
      background-color: #A57B4B;
      @include hoverBgc(#d37d1b); } }

  @include max(768) {
    @include padding(70px 80px);
    .f24 + * {
      @include margin(null 0); } }

  @include max(500) {
    @include padding(40px 30px);
    .login_btn .mr {
      margin-right: 8px; }

    input,
    .login_btn > a {
      height: 45px; } } }


