
/* ==========================================================================
    animation mixin
 @mixin =========================================================================*/


@mixin trans {
  transition: color 0.3s ,opacity 0.3s , background-color 0.3s ease-in-out;
  &:hover {
    transition: color 0.3s ,opacity 0.3s , background-color 0.3s ease-in-out; } }

@mixin hoverText($color) {
  @include trans;
  &:hover {
    color: $color; } }

@mixin hoverBgc($color) {
  @include trans;
  &:hover {
    background-color: $color; } }


@mixin trans2 {
  transition: transform 0.3s ease-in-out;
  &:hover {
    transition: transform 0.3s ease-in-out; } }

@mixin trans2-slow {
  transition: transform 0.6s ease-in-out;
  &:hover {
    transition: transform 0.6s ease-in-out; } }


@mixin hover($type, $sec: 0.3s, $mode: ease-in-out) {
  transition: $type $sec $mode;
  &:hover {
    transition: $type $sec $mode; } }

/* ==========================================================================
    animation
 @mixin =========================================================================*/



@keyframes fade-in-bottom {
  0% {
    transform: translateY(10%);
    opacity: 0; }
  40% {
    opacity: 0.7; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fade-in-top {
  0% {
    transform: translateY(-10%);
    opacity: 0; }
  40% {
    opacity: 0.7; }
  100% {
    transform: translateY(0);
    opacity: 1; } }

@keyframes fade-in {
  0% {
    opacity: 0; }
  50% {
    opacity: 0.7; }
  100% {
    opacity: 1; } }


