
#taste {
  background-image: url("images/login-7-bgc.jpg");
  background-repeat: no-repeat;
  background-position: bottom 80px right 70px;

  .container {
    position: relative;
    padding-top: 60px; }

  .right-info {
    @include position(absolute,null 10px null null );
    width: 520px;
    .f24 {
      color: #A57B4B; }

    li {
      padding-left: 20px;
      @include circle(12px,12px);
      text-indent: -20px; }

    a {
      color: $brown;
      text-decoration: underline;
      text-indent: 0px; } } }

