#taste {
  @include max(1400) {
    .container {
      padding-top: 30px;
      > .site, .right-info {
        @include margin(null auto); } }

    .right-info {
      position: static;
      margin-bottom: 30px;
      width: 100%;
      max-width: 810px; } }

  @include max(500) {

    .site_title .city {
      display: block;
      width: auto;
      @include padding(6px null);
      font-size: 14px; }


    .site_content {
      flex-direction: column;
      align-items: center;
      @include padding(15px 20px);
      ul {
        margin-right: 0; }

      .btn {
        @include margin(10px null null 0);
        border-radius: 5px;
        height: auto;
        @include padding(12px null); } } } }

