.noborder {
  border-width: 0; }

.progress {
  @include f(space-between,center);
  @include margin(null auto);
  max-width: 910px;
  min-width: 500px;
  @include padding(null 10px);
  a {
    @include f(center, center, column);
    position: relative;
    span {
      padding-top: 10px; } }


  [class^="icon"] {
    font-size: 1.9rem;
    color: rgb(149, 149, 149);
    @include padding(null 3px 15px 3px); }

  .current + [class^="icon"]:before {
    content: '\f101';
    color: $brown; }


  @include max(768) {
    font-size: 15px;
    margin-bottom: 10px;
    [class^="icon"] {
      @include padding(null 0px 10px 0px); }
    a {
      span {
        display: none; }
      &:after {
        content: attr(data-abbr);
        margin-top: 10px; }

      &.current {
        color: $brown-dark;
        flex-shrink: 1; }

      &:not(.current) {
        flex-shrink: 6; } } }

  @include max(360) {

    a {
      &.current {
        max-width: 70px; }

      &:not(.current) {
        max-width: 50px; } } } }


.cart-grid {
  .infoCard-wrap {
    position: relative; }

  .infoCard,
  .submit {
    width: calc(100% - 405px); }

  @include min(501) {
    .infoCard {
      @include padding(null 35px); } }

  .infoCard.right {
    width: 380px;
    @include position(absolute, 0 0 null null );

    .infoCard__title span {
      color: #333; }

    .border {
      border-top: 1px solid #eee;
      margin-top: 0.5rem; } }

  [class*="sum__"] {
    @include padding(0px); }

  .submit {
    border-width: 0px;
    .btn {
      max-width: 375px;
      width: 100%;
      height: 50px;
      @include fc;
      @include margin(null 10px); } }

  .infoCard__title > span:first-child {
    transform: translateY(3px); }

  @include max(1365) {
    .infoCard,
    .submit {
      width: 100%; }

    .infoCard.right {
      width: 100%;
      max-width: 718px;
      @include margin(null auto);
      @include position(relative, null 0 null null ); } } }



.infoCard--sm {
  .infoCard__title {
    @include padding(12px null ); }
  .infoCard__content {
    @include padding(8px null ); } }

// ===================================================== 各別頁面

#cart {
  .radio-top {
    .infoCard__content {
      @include f; }

    .radio_unit {
      width: 100px; }

    label {
      justify-content: flex-start;
      &:before {
        margin-right: 15px; } } }


  .del {
    min-width: 0px;
    white-space: nowrap; }

  .icon-trash {
    color: rgb(189, 160, 120);
    font-size: 1.3rem;
    @include hoverText(#ce7208);
    cursor: pointer; }

  .table__title, .table__content {
    @include min(991) {
      @include padding(null 10px); } }

  .select_unit {
    width: 80px; }

  .sub {
    font-size: 15px; }

  .num {
    min-width: 120px;
    @include min(1600) {
      min-width: 80px; } }

  input {
    @include margin(null 8px);
    width: 65px;
    font-weight: bold;
    font-style: italic;
    text-align: center;
    &::placeholder {
      color: $brown-dark; } }

  @include max(990) {
    .select_unit {
      margin-bottom: 5px; }
    .icon-trash {
      @include position(absolute, calc(50% - 16px) 0 null null );
      @include padding(0 26px);
      font-size: 1rem; } }

  @include max(768) {
    .baseline {
      flex-wrap: wrap;
      span {
        width: 100%;
        margin-left: 0;
        margin-top: 10px; } } }

  @include max(450) {
    .icon-trash {
      top: auto;
      bottom: 20px;
      @include padding(0 15px); } }



  .gift__wrap {
    @include f(space-between, stretch);
    max-width: 1000px; }

  .gift {
    @include margin(null 8px);
    @include f(flex-start, flex-start, column);
    width: 100%;
    @include padding(18px 18px);
    border: 1px solid #ddd; }

  .gift__title {
    margin-top: 10px;
    margin-bottom: auto;
    width: 100%; }

  .gift__pic {
    border: 1px solid #eee;
    .fluid {
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center center; }
    &:before {
      padding-top: 100%; } }


  .gift__bottom {
    margin-top: 15px;
    width: 100%;
    @include f(space-between,flex-end);
    .check_unit {
      span {
        color: $brown-dark; }
      .box {
        margin-right: 10px; } } }

  @include max(990) {
    .gift {
      font-size: 15px; }
    .gift__wrap {
      min-width: 750px; } }

  @include max(400) {
    .gift {
      font-size: 14px;
      @include padding(10px 10px); }

    .gift__wrap {
      min-width: 700px; }

    .gift__bottom {
      flex-wrap: wrap; }

    .gift__num,
    .gift__check {
      width: 100%;
      margin-bottom: 8px; } }



  .js-soldOut {
    .gift__num {
      span {
        display: none; }
      &:before {
        content: "無庫存"; } }

    .gift__check {
      background-color: rgb(154, 154, 154);
      color: white;
      @include size(70px, 30px);
      border-radius: 15px;
      @include fc;
      .check_unit {
        display: none; }
      &:before {
        content: "已贈罄"; } } } }



#modal-gift {
  @include size(650px, 330px);
  @include padding(30px);
  max-width: 100% !important;
  @include f;
  text-align: left;

  .pic {
    @include size(175px);
    flex-shrink: 0; }
  .wrap {
    margin-left: 25px; }
  .f20 {
    margin-bottom: 12px;
    padding-bottom: 12px;
    border-bottom: 1px solid #eee; } }



#info {
  .infoCard--form {
    @include f;
    flex-wrap: wrap;
    @include margin(null -18px); }

  .line {
    width: calc(50% - 36px);
    @include margin(null 18px); }

  .radio_wrap {
    padding-left: 5px;
    label {
      margin-right: 30px;
      justify-content: flex-start;
      &:before {
        margin-right: 12px; } } }

  .addr {
    width: 100%;
    > *:not(.main) {
      margin-right: 12px;
      max-width: 120px; } }

  .select_unit {
    width: 100%; }

  .eli {
    padding-right: 35px; }

  .infoCard__title {
    .wrap {
      margin-left: auto;
      @include fc;
      >*:not(:first-child) {
        margin-left: 25px; } }
    .box {
      @include size(15px);
      margin-right: 12px; } }

  .tri-type {
    width: 100%;
    white-space: nowrap;
    .wrap {
      height: 40px;
      @include f(flex-start, center); }
    .check_unit {
      margin-right: 30px; } }

  .ps {
    .infoCard__content {
      padding-bottom: 25px; }
    .line {
      width: calc(100% - 36px);
      border-bottom: 1px solid #ddd; }
    ul {
      @include margin(null 18px); } } }


#info {
  @include max(990) {
    .addr {
      flex-wrap: wrap; }

    .line.lg-break {
      width: 100%; } }

  @include max(767) {
    .infoCard__title .wrap {
      margin-top: 10px; } }

  @include max(650) {

    .addr {
      .select_unit {
        width: calc(50% - 6px);
        max-width: 100%;
        &.break {
          margin-right: 0; } }

      input.break {
        width: 100%;
        max-width: 100%;
        margin-right: 0; } }

    .line {
      width: 100%; }
    .radio_wrap {
      padding-left: 96px; }

    .tri-type {
      height: 90px;
      .wrap {
        width: 100%;
        height: auto;
        flex-wrap: wrap; }

      .check_unit {
        width: 100%;
        @include margin(null 0px 8px null ); } } }


  @include max(380) {
    font-size: 14px;
    .line {
      padding-left: 76px;
      > span:first-child {
        width: 56px; } }
    select,
    input {
      @include padding(null 8px); } } }


#pay {
  .infoCard__content.pay {
    @include padding(0 0 12px 0); }
  .radio_unit {
    height: auto;
    @include padding(18px null ); }
  .radio_unit:not(:last-child) {
    border-bottom: 1px dashed #ddd; }


  .radio_unit label {
    justify-content: flex-start;
    align-items: flex-start;

    &:before {
      margin-top: 3px; }

    > span:first-child {
      width: 140px;
      margin-left: 12px;
      flex-shrink: 0; }

    .gray {
      white-space: normal; }

    .gray.img {
      @include f(flex-start, center); }

    @include max(600) {
      flex-wrap: wrap;
      .gray {
        margin-top: 10px;
        padding-left: 30px; }

      .gray.img {
        flex-wrap: wrap;
        > div {
          width: 100%;
          margin-bottom: 6px; }
        img {
          margin: 0; } } } }

  .ps2 a {
    color: $brown-dark;
    @include hoverText(#d8801c); } }


#confirm {
  .italicNum {
    letter-spacing: 1px;
    color: $brown-dark; } }

#confirm,
#complete {
  .infoCard--list {
    ul {
      @include f(space-between, flex-start, row, wrap); }
    li {
      width: calc(50% - 18px); }
    li.w100 {
      width: 100%; }

    ul.bd {
      border-bottom: 1px solid #eee; }

    ul.red li {
      width: 100%;
      padding: 0; } }

  @include max(768) {
    .infoCard {
      font-size: 15px;
      li {
        width: 100%;
        @include padding(3px null ); } } }

  @include max(360) {
    .infoCard,
    .infoCard .infoCard--list {
      font-size: 14px; } } }

#complete {
  .success {
    border-color: rgb(214, 142, 143);

    .f24 {
      color: rgb(206, 107, 107);
      width: 100%; }

    .infoCard--list {
      span:nth-child(2) {
        color: rgb(206, 107, 107); } } } }





