

.intro__head {
  @include f(center, flex-start);
  margin-bottom: 10px;
  width: 100%;
  @include padding(40px 30px); }

.head__left {
  margin-right: 35px;
  width: 50%;
  max-width: 400px;

  .show {
    margin-bottom: 20px;
    background-size: cover;
    background-position: center center;
    &:after {
      display: block;
      content: "";
      padding-top: 100%; } }

  .slick-intro {
    @include margin(null auto);
    width: 100%;


    .unit {
      width: 30%;
      background-size: cover;
      @include margin(null 5px);
      opacity: 0.7;
      cursor: pointer;
      position: relative;
      &:after {
        display: block;
        padding-top: 100%;
        content: ""; }
      @include trans;
      &:hover:not(:focus) {
        opacity: 0.9; }
      &:focus {
        opacity: 1;
        outline: none; } } }

  .slick-list {
    max-width: 300px;
    width: calc(100% - 100px);
    @include margin(null auto); }

  .slick-arrow {
    color: #7c7c7c;
    @include size(40px);
    @include position(absolute, 50% nul nul nul  );
    transform: translateY(-50%);
    @include fc;
    cursor: pointer;
    &:before {
      font-size: 16px; } }

  .slick-arrow.slick-disabled {
    color: rgba(200,200,200,0.7); }

  .icon-next {
    right: 10px; }
  .icon-prev {
    left: 10px; } }


.head__right {
  width: 100%;
  .f24 {
    margin-bottom: 20px; }

  .f16.lh20 {
    width: 100%; }


  div[class*="border-"] {
    border-bottom: 1px solid rgb(239,239,239); }

  .border-1 {
    margin-bottom: 12px;
    padding-bottom: 12px; }
  .border-2 {
    margin-bottom: 20px;
    padding-bottom: 16px; }
  .border-3 {
    padding-bottom: 20px;
    margin-bottom: 20px; }

  .group {
    @include f;
    > span {
      width: 80px;
      flex-shrink: 0;
      color: #7c7c7c; } }

  .control {
    border: 1px solid #ccc;
    border-radius: 3px;
    @include f;
    text-align: center;

    .mount {
      @include size(42px, 32px);
      border-right: 1px solid #ccc;
      border-left: 1px solid #ccc; }
    a {
      @include size(32px);
      color: #ccc;
      @include hoverText(#000); } }

  .through {
    text-decoration: line-through;
    color: #7c7c7c; }

  .sale {
    line-height: 1;
    @include f(flex-start, flex-end);

    .f28 {
      color: #cc0000;
      font-family: 'Open Sans', sans-serif; } }

  .links {
    .btn {
      color: white;
      border-radius: 5px;
      height: 35px; }

    .btn:first-child {
      margin-right: 10px;
      flex: 1 1 250px;
      background-color: rgb(221, 103, 75);
      @include hoverBgc(#cc0000); }

    .btn:last-child {
      flex: 1 1 150px;
      background-color: rgb(140, 176, 102);
      @include hoverBgc(rgb(101, 160, 38));

      &:before {
        transform: scale(-1,1);
        margin-right: 10px; } } }

  .share {
    a {
      margin-right: 8px; }
    img {
      padding-bottom: 2px; } }

  .trace {} }

.icon-heart { //跟首頁通用
  color: #ccc;
  cursor: pointer;
  &.active {
    color: rgb(207, 63, 62) !important; }
  @include hoverText(rgb(207, 63, 62)); }


@include max(991) {
  .intro__head {
    flex-direction: column-reverse;
    align-items: center; }
  .head__left {
    margin-right: 0;
    width: 100%; }

  .head__right {
    margin-bottom: 20px; } }

@include max(768) {
  .intro__head {
    @include padding(20px 15px); }

  .head__left {
    margin-right: 0; } }

@include max(380) {//slick-intro 不知為何會推擠右側，沒有依寬度100%設定，應該是因為slick設定寬度

  .head__left {
    padding-bottom: 80px;
    position: relative;
    .slick-intro {
      position: absolute; }

    .slick-list {
      width: calc(100% - 50px); }
    .icon-next {
      right: -15px; }
    .icon-prev {
      left: -15px; } } }



// .items.midBlock.form-type
// 三頁的產品介紹頁面，有包含表格
.form-type {
  .scrollbarX {
    width: 84vw;
    >table {
      min-width: 450px;
      width: 450px; } } }

[class*="form-type-"] {
  @include min(992) {
    .head__left {
      width: 40%;
      max-width: 320px; }
    .head__right {
      width: calc(60% - 20px); } } }


[class*="form-type-"] .radio {
  margin-top: 10px;
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
  text-align: center;
  white-space: nowrap;

  tr {
    border: 1px solid rgb(211, 177, 131); }
  th,
  td {
    padding: 0 10px;
    height: 45px; }
  th {
    background-color: rgb(249, 245, 234); }
  td {
    background-color: rgb(255, 252, 247); } }


[class*="form-type-2"] .radio {
  tr {
    > *:first-child {
      width: 75px; } } }

[class*="form-type-3"] .radio {
  tr {
    > *:first-child {
      width: 100px;
      line-height: 1.5; } }
  td {
    height: 75px; } }


[class*="form-type-4"] .radio {
  text-align: left;
  th,
  td {
    @include padding(null 28px); }
  tr > td:not(:last-child) {
    border-right: 1px solid rgb(211, 174, 130); }
  label {
    cursor: pointer;
    @include size(100%);
    @include f(flex-start, center); } }




















.intro__main {
  background-color: rgb(255, 255, 255);
  .page {
    height: 60px;

    a {
      @include size(50%, 100%);
      @include fc;
      display: inline-flex;
      border-bottom: 3px solid rgb(229,229,229);
      color: #7c7c7c;

      position: relative; }
    a.active {
      border-bottom: 3px solid #b58a5a;
      color: #b58a5a; }
    a:not(:last-child) {
      &:after {
        display: block;
        content: "";
        border-right: 1px solid rgb(229,229,229);
        height: 24px;
        @include position(absolute, null 0 null null ); } } } }


.content {
  @include padding(32px);
  @include f; }


.content__pic {
  @include f(center, center, column);
  flex-shrink: 0;
  img {
    @include size(215px, 160px );
    margin-bottom: 10px; } }

.content__text {
  margin-right: 30px;
  width: 100%;
  .f24 {
    border-bottom: 1px solid rgb(239,239,239);
    padding-bottom: 14px;
    margin-bottom: 16px; }

  .vege {
    margin-left: 15px; }

  p {
    color: #7c7c7c; }

  .dark {
    color: #1e1e1e; }

  .week {
    margin-bottom: 15px;
    margin-left: 10px; }

  .group {
    @include f;
    color: #7c7c7c;
    &:not(:last-child) {
      padding-bottom: 18px;
      margin-bottom: 18px;
      border-bottom: 1px solid rgb(239,239,239); }

    div:first-child {
      width: 85px;
      flex-shrink: 0; } }



  //.js-page2
  .red {
    color: #D2655C; }
  .orange {
    color: #FF8C00; }
  .bus {
    position: relative;
    img {
      @include position(absolute, 0 0 null null ); } }

  .orange.f32 {
    font-style: italic;
    @include margin(null 15px);
    @include hoverText(#f46a00); }

  .img-group {
    @include f; }

  .info__title {
    height: 40px;
    @include fc; }

  .info__content {
    @include f;
    > div {
      flex: 1 1 auto;
      height: 140px;
      @include f(flex-end,center,column);
      padding-bottom: 20px; }
    span {
      text-align: center;

      margin-top: 10px;
      line-height: 1.4; }
    img {
      max-width: 60%; } }

  .left-group {
    flex: 2;
    .info__title {
      background-color: #BD9F77;
      color: white; }

    .info__content {
      color: #7c7c7c;
      > div:nth-child(odd) {
        background-color: rgb(239, 230, 221); }
      > div:nth-child(even) {
        background-color: rgb(247, 244, 239); } } }


  .right-group {
    flex: 1;
    .info__title {
      background-color: #F7F3ED;
      color: #AD8555; }
    .info__content {
      background-color: #AD8555;
      color: white; }

    .transY {
      padding-bottom: 12px; } } }


.js-page1 ~ * {
  display: none; }

.intro__main {
  @include max(990) {
    .content {
      flex-direction: column; }

    .content__pic {
      @include f(flex-start, flex-start, row, wrap);
      @include margin(null -5px);
      img {
        width: calc(33% - 10px);
        @include margin(null 5px);
        height: auto; } } }

  @include max(500) {
    .content {
      padding: 20px; }

    .content__pic {
      img {
        width: calc(50% - 10px);
        @include margin(null 5px); } }

    .group {
      flex-wrap: wrap;

      div:first-child {
        width: 100%;
        margin-bottom: 5px; } } }



  //.js-page2

  @include max(1100) {
    .bus {
      @include f;
      flex-direction: column-reverse;
      margin-top: -10px;
      img {
        position: relative;
        align-self: flex-end; } } }

  @include max(991) {
    .img-group {
      flex-direction: column;
      width: 100%; }


    .right-group,
    .left-group {
      width: 100%;
      @include f(flex-start, stretch); }

    .left-group {
      height: 135px;
      .info__content {
        max-width: 480px; } }
    .right-group {
      height: 145px;
      .info__content {
        max-width: 240px; } }

    .info__title {
      flex-shrink: 0;
      width: 40px;
      height: 100%;
      line-height: 1;
      writing-mode: vertical-lr; }


    .info__content {
      width: 100%;
      height: 100%;

      > div {
        height: 100%; } } }

  @include max(500) {
    .info__content {
      font-size: 14px;
      > div {
        padding-bottom: 10px; } }



    .right-group {
      height: 130px; }
    .left-group {
      height: 240px;
      .info__content {
        height: 120px;
        max-width: 240px;
        flex-wrap: wrap;
        div {
          width: 50%; }
        div:nth-child(3) {
          order: 1; } } } } }

