

#detail,
#return,
#returning {
  font-family: "Roboto", Heiti TC, "微軟正黑體" , Microsoft JhengHei;

  .container {
    @include f(space-between, stretch, row, wrap); }
  .bottom {
    @include fc;
    width: 100%;
    > a {
      @include fc;
      @include size(200px, 38px);
      border-radius: 3px;
      letter-spacing: 1px;
      color: white;
      background-color: rgb(179, 139, 90);
      @include hoverBgc(#ce8327);
      @include margin(null 12px); }
    .orange {
      background-color: rgb(235, 122, 28);
      @include hoverBgc(rgb(234, 105, 0)); } } }


.icon-thermometer {
  @include size(120px, 40px);
  color: white;
  @include fc;
  border-radius: 20px;
  &:before {
    font-size: 1.2rem;
    @include margin(null 6px null -6px); }

  &.normal {
    background-color: rgb(242, 141, 25); }
  &.low {
    background-color: rgb(92, 199, 215); } }


//咖啡色上框的容器，內容分為二種 multiple 及 list
.infoCard {
  width: calc(50% - 18px);
  background-color: #fff;
  border-top: 5px solid rgb(234, 217, 197);
  border-radius: 5px;
  @include padding(null 28px);
  &.infoCard.w100 {
    width: 100%; }

  @include max(500) {
    @include padding(null 18px); }
  @include max(359) {
    @include padding(null 12px); } }


.infoCard__title {
  @include padding(20px null);
  border-bottom: 1px solid rgb(234, 217, 197);
  width: 100%;
  @include f(flex-start,flex-end, row, wrap);

  > span:first-child {
    margin-right: 20px;
    color: #B47832; }

  a {
    margin-left: auto;
    display: inline-block; }

  @include max(767) {
    @include padding(12px null);
    > span:nth-child(2) {
      width: 100%;
      margin-top: 10px; } }

  @include max(400) {
    @include padding(10px null); } }



.infoCard__content {
  @include padding(25px null 35px); }





//第一種類型內容 infoCard--multiple
// 內容分為三種樣式 multiple__main  multiple__secondary multiple__sum

//第二種類型內容
.infoCard--list {
  letter-spacing: 1px;
  @include padding(23px null);
  li:not(.tel) {
    @include padding(5px null);
    @include f; }

  span:first-child {
    margin-right: 45px;
    width: 68px;
    display: inline-block;
    color: #1E1E1E;
    flex-shrink: 0; }

  span:nth-child(2) {
    color: #7C7C7C; }

  &.two-columns {
    li {
      width: 49%;
      display: inline-block; } } }

.tel {
  @include f(flex-start, baseline,row,wrap);
  white-space: nowrap; }

span.sp {
  text-align: justify;
  margin-bottom: -68px;

  &:after {
    content: "";
    display: inline-block;
    width: 100%; } }




.infoCard {

  .infoCard--list {
    @include max(1200) {
      &.two-columns li {
        width: 100%; } }

    @include max(991) {
      span:first-child {
        margin-right: 30px; } } }

  @include max(767) {
    width: 100%;

    .infoCard__content {
      @include padding(12px null 22px); } }

  @include max(400) {

    .infoCard__content {
      @include padding(10px null); }

    .infoCard--list {
      font-size: 15px;
      span:first-child {
        margin-right: 20px; } } } }


//第一種類型內容
//內容分為三種樣式 multiple__main  multiple__secondary multiple__sum
.infoCard--multiple {}


.multiple__main {  //multiple 中第一種
  table {
    border-radius: 5px 5px 0 0;
    overflow: hidden; }

  tr {
    background-color: rgb(245, 241, 240);
    &:first-child {
      background-color: rgb(213, 178, 136); }
    &:last-child {
      border: 0px solid #000; } }

  td {
    @include padding(1rem null);
    &::not(:last-child) {
      border-right: 1px solid white; } }

  .fc {
    height: 48px;
    @include fc; }

  @include max(990) {
    max-width: 450px;
    margin-bottom: 40px;
    @include margin(null auto null null);
    font-size: 15px;
    th {
      display: none; }
    td {
      @include f;
      padding: 0;
      height: 50px;
      &:before {
        @include fc;
        content: attr(data-title);
        width: 40%;
        height: 100%;
        color: white;
        background-color: rgb(213, 178, 136); }

      .fc {
        @include padding(15px null);
        width: 60%;
        height: 100%; } }
    td:nth-child(odd) {
      &:before {
        background-color: #cea579; }
      .fc {
        background-color: #ede6e6; } } } }



//multiple 中第二種 multiple__secondary

.table__title,
.table__content {
  @include f(space-between, center);
  @include padding(null 25px);

  > div {
    min-width: 120px;
    @include padding(20px 10px);

    &.name ~ div {
      text-align: center; } }

  .pic {
    width: 140px;
    img {
      width: 100px; } }
  .name {
    width: 30%;
    flex: 1 1 30%;
    color: #683700;
    .sub {
      color: #b7a287;
      margin-top: 6px; } }

  .num {
    width: 70px; }

  @include min(1600) { //讓div有間距，並在螢幕縮小時會消失
    > div {
      &:not(.pic):not(.name):not(.check) {
        @include margin(null null null 40px); }
      &.name {
        @include margin(null null null 10px); } } }

  .check { //return.html
    min-width: 80px;
    .check_unit {
      height: 50px;
      @include fc; } } }


.table__title {
  background-color: rgb(234, 217, 197);
  color: #a38468;
  & ~ div {
    border: 1px solid #ddd;
    border-width: 0 1px 1px 1px;
    position: relative; }

  > div {
    @include padding(13px null); } }

// .table__content

.table__total {
  text-align: right;
  @include padding(null 25px);
  > * {
    @include padding(14px null);
    display: inline-block;
    &:first-child {
      color: #7C7C7C; }
    &:last-child {
      font-weight: bolder; } } }

.table__append {
  @include padding(12px 25px);
  color: #e09aae;

  &:after, &:before {
    content: "";
    display: block;
    @include size(18px);
    background-color: #fff;
    @include position(absolute, -5px null null null ); }
  &:after {
    left: 0; }
  &:before {
    right: 0; } }


@include max(990) {

  .table__title,
  .table__content .per,
  .table__content .state {  //js判斷顯示
    display: none; }

  .table__content {
    flex-wrap: wrap;
    min-height: 160px;
    @include padding(25px 15px 25px 165px);

    &:nth-child(2) {
      border-top: 1px solid #ddd; }

    > div:not(.pic) {
      width: 100%;
      @include padding(3px 5px);
      &.name ~ div {
        text-align: left; } }

    .num:before,
    .check:after {
      content: attr(data-title);
      margin-right: 5px;
      display: inline-block; }

    .pic {
      @include padding(30px 38px);
      width: auto;
      @include position(absolute, 0 null null 0); } } }


@include max(500) {
  .table__append {
    font-size: 14px;
    @include padding(8px 15px); }

  .table__content {
    @include padding(15px 8px 15px 85px);
    min-height: 0px;
    font-size: 15px;

    .name .sub {
      margin-top: 0px;
      font-size: 14px; }

    .pic {
      @include padding(20px 8px);
      img {
        width: 70px; } } } }





//multiple 中第三種 multiple__sum
.multiple__sum {}
[class*="sum__"] {
  @include padding(17px 25px 17px calc(100% - 425px));
  li {
    @include f(flex-start, center); }
  span {
    margin-left: auto; }
  a {
    text-decoration: underline;
    font-weight: bolder;
    @include hoverText(#A57B4B); } }

.sum__cost {
  border: 1px solid #ddd;
  border-width: 0 1px 1px 1px;
  @include padding(8px null); }

.sum__return {
  border: 1px solid #ddd;
  background-color: rgb(243, 243, 243); }


@include max(450) {
  [class*="sum__"] {
    font-size: 15px;
    @include padding(null 10px); } }


@include max(340) {
  .sum__return {
    font-size: 14px; }

  [class*="sum__"] {
    @include padding(null 6px); } }

#detail.guest {
  .edit {
    color: rgb(235, 122, 28);
    @include hoverText(rgb(234, 105, 0));
    text-decoration: underline; }
  .sum__order {
    border: 1px solid #ddd;
    border-width: 0 1px 1px 1px; } }
