
// tmp 防止一開始的破圖
.slider-show {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s ease; }
.slider-show.slick-initialized {
  visibility: visible;
  opacity: 1; }



$bgco: rgb(245, 241, 240);



.slider-show {
  border-bottom: 1px solid rgb(181,137,89);
  width: 100%;
  .pic {
    background-size: cover;
    background-position: center;
    position: relative;

    &:after {
      display: block;
      content: "";
      padding-top: 33.57%; }  //470px

    &:before {
      @include size(100%);
      @include position(absolute, 0 null null 0);
      z-index: 1;
      content: "";
      display: block;
      background-color: rgba(black, 0.3);
      opacity: 0;
      transition: opacity 0.3s; } }

  .pic:not(.slick-current) {
    &:before {
      opacity: 1;
      transition: opacity 0.3s; } }

  .slick-list {
    @include padding(null 13.5vw); }

  .slick-arrow {
    @include position(absolute, 0 null null null );
    @include size(13.5vw, 100%);
    color: white;
    z-index: 1;

    &:before {
      font-size: 1.2rem;
      margin: 0 20px;
      transition: transform 0.3s; }

    &:hover {
      &:before {
        transform: scale(1.3);
        transition: transform 0.3s; } }

    &.slick-disabled {
      background-color: rgba(200,200,200,0.5); } }

  .icon-prev {
    left: 0;
    @include f(flex-end, center); }
  .icon-next {
    right: 0;
    @include f(flex-start, center); } }



.slider-nav {
  height: 50px;
  @include fc;

  >li {
    width: 200px;
    text-align: center;
    cursor: pointer;
    color: rgb(102, 102, 102);
    line-height: 50px;
    @include hoverText($brown-dark);

    position: relative;
    &:after {
      content: "";
      display: block;
      border-right: 1px solid rgb(181,137,89);
      height: 16px;
      @include position(absolute, 17px 0 null null ); } }

  .active {
    color: $brown-dark;
    &::before {
      border-left: 1px solid rgb(181,137,89);
      border-top: 1px solid rgb(181,137,89);
      content: "";
      display: block;
      @include size(12px);
      @include position(absolute, -7px null null 50%);
      background-color: $bgco;
      transform: translateX(-50%) rotate(45deg); } } }


.slider-show {
  @include max(1366) {
    .slick-list {
      @include padding(null 8vw); }

    .slick-arrow {
      @include size(8vw, 100%); } }

  @include max(767) {
    .slick-list {
      @include padding(null 0); }
    .slick-arrow {
      display: none; } } }


.slider-nav {
  @include max(990) {
    display: none; } }



.submenu {
  @include f(flex-start, flex-end);
  margin-bottom: 20px;
  a {
    @include fc;
    font-weight: bold;
    color: rgb(162, 134, 104);
    @include margin(null 1px null null );
    @include size(200px, 50px);
    border: 1px solid rgb(217,194,170);
    border-radius: 8px 8px 0 0;
    background: linear-gradient(to bottom, rgb(255,255,255), rgb(249,245,235));
    margin-bottom: 10px;

    position: relative;
    &::before {
      content: "";
      display: block;
      background: rgb(104,55,0);
      @include size(16px);
      @include position(absolute, null null 0px 50%);
      transform: translate(-50%,50%) rotate(45deg);
      opacity: 0; } }



  a.active {
    background: rgb(104,55,0);
    color: white;
    height: 60px;
    &::before {
      opacity: 1; } } }


.submenu {
  .slick-track {
    @include f(flex-start, flex-end); }
  a {
    display: flex !important; }

  .slick-list {
    @include margin(null 36px); }

