  .icon-next {
    right: 0; }

  @include max(767) {
    .slick-list {
      @include margin(null 36px);
      // +padding(null 1px)
      font-size: 12px; } }

  @include max(430) {
    .slick-list {
      @include margin(null 30px); }

    a {
      height: 38px; }
    a.active {
      height: 42px; } }

  .slick-arrow {
    @include size(24px);
    top: 10px;
    &:before {
      font-size: 12px; } }

  .icon-prev {
    left: -5px; }
  .icon-next {
    right: -5px; } }



