.catalog {
  margin-bottom: 15px;
  .block__title {
    z-index: 11;
    position: relative; }
  #dorpdown-js {
    background-color: transparent; }

  ul {
    background-color: rgb(255,255,255);
    @include padding(null 25px);
    margin-bottom: 8px;
    @include padding(10px null);
    font-weight: bold; }

  .subtitle {
    font-weight: bold;
    line-height: 2.25;
    color: #a57b4b;
    a {
      color: #a57b4b; }
    &:before {
      @include margin(null 10px null -3px); } }

  li:not(.subtitle) {
    line-height: 2;
    @include f(null , center);
    @include circle( 5px, 10px, #c8ab81);
    a {
      @include hoverText(rgb(104,55,0)); } }


  ul.purple {
    .subtitle {
      &::before, a {
        color: #8c1e5b; } }
    .active {
      &::before {
        background-color: #8c1e5b; }
      a {
        color: #8c1e5b; } } }

  ul.orange {
    .subtitle {
      &::before, a {
        color: #fd5f27; } }
    .active {
      &::before {
        background-color: #fd5f27; }
      a {
        color: #fd5f27; } } }

  @include max(991) {
    font-size: 15px;
    li:not(.subtitle):before {
      background-color: #fff !important; }

    ul {
      @include padding(null 20px); }

    .subtitle:before {
      margin-right: 5px; } }


  @include max(767) {
    .block__title {
      width: 100%; }

    #dorpdown-js {
      @include f(flex-start, stretch, row, wrap);
      width: 100%; }

    ul {
      width: 50%; }

    #dorpdown-js {
      position: absolute;
      z-index: 11;
      background-color: #efefef;
      display: none; } }


  @include max(450) {

    font-weight: normal;
    ul {
      width: 100%; }
    .subtitle + div {
      display: none; } } }


#close-bgc {
  @include size(100vw, 100vh);
  @include position(fixed, 0 0 null null );
  z-index: 10;
  pointer-events: none;
  transition: 0.3s background-color;
  &.active {
    transition: 0.3s background-color;
    pointer-events: auto;
    background-color: rgba(black, 0.1); } }










//問題在於 順序無法以 小大小小 排列
//應該要用js方式變更DOM 或是放入dashboard的方式


.items__content {

  @include f(space-around, flex-start, row, wrap);
  @include padding(5px 18px);

  .card {
    // +size(220px, auto)
    width: calc(100% / 4);
    white-space: normal;
    @include padding(15px);
    @include margin(18px null 18px null ); }

  .card__pic {
    margin-bottom: 10px;
    position: relative; }

  .card__pic[data-tri]:before {
    content: attr(data-tri);
    @include position(absolute, 0 null null 0);
    color: white;
    @include triangle("up", 74px, 37px, red);
    font-size: 14px;
    line-height: 45px;
    z-index: 1;
    text-indent: -14px;
    white-space: nowrap;
    transform:  translateY(15px) rotate(-45deg);
    transform-origin: 0 100%; }

  .card__pic[data-corner]:after {
    content: attr(data-corner);
    @include position(absolute, null 0 0  null);
    @include size(55px, 25px);
    @include fc;
    background-color: rgb(255, 156, 1);
    border-radius: 6px 0 0 0;
    color: white;
    font-size: 14px;
    z-index: 1; }

  .cover {
    @include position(absolute, 0 0 0 0);
    @include size(100%);
    @include fc;
    background-color: rgba(black, 0.3);
    opacity: 0;
    pointer-events: none;
    @include hover(opacity);

    [class^="icon-"] {
      @include size(50px);
      @include margin(null 10px);
      border-radius: 50%;
      @include fc;
      cursor: pointer;
      flex-shrink: 0; }

    .icon-heart {
      color: rgb(189, 160, 120);
      background-color: #fff;
      @include padding(3px 2px null null );

      &:before {
        @include hover(color); }
      &:hover:before {
        color: #d2655c; } }

    .icon-cart {
      color: #fff;
      background-color: rgb(189, 160, 120);
      transition: 0.3s all;

      @include hover(background-color);
      &:hover {
        background-color: $brown-dark; } } }

  .card:hover {
    box-shadow: 0 0 1px #b47832;
    .cover {
      opacity: 1;
      // transition: 0.3s opacity
      pointer-events: auto; } }


  .card__name {
    line-height: 1.625;
    @include padding(3px null); }

  .card__price {
    margin-bottom: 5px;
    span:first-child {
      font-size: 15px;
      white-space: nowrap;
      display: inline-block;
      color: #8b8b8b;
      margin-right: 5px; }
    .num {
      font-size: 18px;
      white-space: nowrap;
      color: #b47832; } }


  .card__ps {
    @include size(38px , 20px);
    background-color: #d2655c;
    border-radius: 3px;
    color: white;
    @include fc;
    font-size: 14px; }


  .border {
    width: calc(100% + 36px);
    @include margin(null -18px);
    border-bottom: 1px solid #e5e5e5;
    @include max(1450) {
      display: none; } }



  @include max(990) {
    align-items: stretch;
    .card {
      font-size: 15px;
      width: calc(100% / 3);
      @include padding(8px);
      @include margin(8px null);
      @include f(flex-start, flex-start, column); }
    .card__name {
      margin-bottom: auto; } }

  @include max(768) {
    .cover {
      [class^="icon-"] {
        @include size(45px);
        @include margin(null 6px); } } }



  @include max(530) {
    @include padding(null 6px);
    font-size: 14px;
    .card__price span:first-child {
      font-size: 14px; }

    .card {
      width: calc(50%);
      @include padding(null 6px);
      @include margin(0 null 10px); }

    .card__pic {
      margin-bottom: 3px; }

    .card__price {
      margin-top: 5px; } } }






